<template>
  <div class="card">
      <div class="card-content">
          <div class="media">
      <div class="media-left">
        <!-- is-48x48 keeps the user image square-->
        <figure class="image is-48x48">
          <img 
          :src="post.user.pic" 
          :alt="post.user.name">
        </figure>
      </div>
      <div class="media-content">
        <p class="title is-4">{{post.user.name}}</p>
        <p class="subtitle is-6">{{post.user.handle}}</p>
      </div>
    </div>
      </div>
  <div class="card-image">
    <!-- we don't want an image ratio here, otherwise image aspect ratio will get smooshed -->
    <figure class="image">
      <img :src="post.src" :alt="post.alt">
    </figure>
  </div>
  <div class="card-content">

    <div class="content">
      {{post.caption}}
      <time :datetime="post.time"></time>
    </div>
  </div>
</div>
</template>

<script>
export default {
    props: {
        post: Object
    }

}
</script>

